@charset "UTF-8";

div.sphp-back-to-top-button {
  position: fixed;
  bottom: rem-calc(15);
  right: rem-calc(45);
  padding: 0;
  display: none;
  font-size: rem-calc(80);
  color: #222;
  svg, i {
    cursor: pointer;
    //@include single-text-shadow(0, 0, 4px, 2px, #ff8000);
    //@include text-shadow(#333 0px 0px 3px, rgba(#FF8000, 0.6) 2px 2px 1px, rgba(#FF8000, 0.2) -2px -2px 1px);
    @include transition(all, 0.5s, ease-in-out);
    @include opacity(0.6);
    &:hover {
      color: #ec5840;
      @include opacity(0.75);
      @include transform(rotate(360deg));
    }
  }

}
