@mixin sphp-anytime-input() { 
  input.sphp.AnyTimeInput {
    background-color: #fff;
    cursor: default;

    &:after {
      content: "\f087"; 
      font-family: FontAwesome;
      font-style: normal;
      font-weight: normal;
      text-decoration: inherit;
      position: absolute;
      font-size: 400px;
      color: lightblue;
      top: 50%;
      left: 50%;
      margin: -300px 0 0 -200px;
      z-index: 1;
    }
  }
}

@mixin sphp-select() { 
  select::-ms-expand {	
    display: none; 
  }
  select {
    -webkit-appearance: none;
    appearance: none;
    option[selected] {
      border-bottom: solid 1px #ccc;
    }
  }
}

@mixin sphp-abide-form-error-callout() {
  form .callout[data-abide-error] {
    color: get-color(alert);
  }
}
@mixin sphp-validable-input() {
  .sphp.validable-input {
    &.required {
      label.main-label {
        &:after {
          content: '*';
          color: red;
          font-weight: bold;
          margin-left: rem-calc(5);
        }
      }
    }
  }
}
@mixin sphp-switch-board() { 
  .sphp.switch-board { 
    .description {
      font-weight: bold;
      font-size: rem-calc(15);
      padding-bottom: 1rem;
    } 
    .switch-toggle-wrapper {
      display: flex;
      margin-bottom: 1rem;
      justify-content: space-between;
      align-items: center;
      .switch {
        margin-bottom: 0;
      }
    }
    .options {
      padding-left: 1rem;
    }
  }
}